.list-group-item {
  padding: 15px 20px;
  border-bottom: 1px solid #3d5a80;
}

.list-group-item:last-child {
  border-bottom: none;
}

.list-group-item span {
  line-height: 35px;
  font-size: 20px;
  cursor: pointer;
}

.list-group-item input {
  width: 100px;
  line-height: 35px;
  font-size: 20px;
  text-align: center;
  border: 0;
}

.list-group-item button {
  width: 35px;
  height: 35px;
  margin: 3px;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.list-group-item .btn-cookie {
  color: #e09f3e;
}

.list-group-item .btn-trash {
  color: #e5383b;
}

.list-group-item .fa-star {
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  font-size: 16px;
  color: #ffd700;
  transition: 0.3s all;
  transform: translateX(30px);
  opacity: 0;
}

.list-group-item.like .fa-star {
  opacity: 1;
  transform: translateX(0px);
}

.list-group-item.increase .list-group-item-label,
.list-group-item.increase .list-group-item-input {
  color: #e09f3e;
}

.list-group-item.increase .btn-star {
  color: #aeaeae;
}

.empControl {
  display: flex;
}

@media (min-width: 768px) {
  .list-group-item span {
    white-space: nowrap;
  }
  .list-group-item span:hover {
    transition: transform 0.2s ease;
    transform: scale(1.1);
  }
  .list-group-item-input:hover {
    transition: transform 0.2s ease;
    transform: scale(1.1);
  }
  .list-group-item input {
    margin-right: 20px;
  }
}

@media (max-width: 540px) {
  .list-group-item input,
  .list-group-item span {
    font-size: 16px;
  }
  .btns-group .btn-sm {
    width: 30px;
    height: 30px;
  }
  .list-group-item {
    padding: 15px 10px;
  }
}
